body {
  background: white;
  margin: 0;
  height: 100vh;
}

#home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  display: inline-block;
  font-family: 'Sacramento', cursive;
  letter-spacing: .25em;
  text-align: left;
  margin: 5vh 0 0 0;
  user-select: none;
}

#filter-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 25px;
  border: black solid 1px;
  width: 35%;
  margin: 5vh 0 5vh 0;
}

#filter-input {
  width: 90%;
  margin-left: 1em;
}

#filter-input, #filter-input:hover {
  border-color: transparent;
  outline: none;
}

#filter-clear {
  cursor: pointer;
  margin-right: 1em;
}

.polaroid {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1;
}

.polaroid-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 95%;
  aspect-ratio: 1;
  background: whitesmoke;
  padding-top: 5%;
  box-shadow: 0px 2px 7px -1px black;
  border: 1px solid black;
}

.polaroid-card>.pin {
  position: absolute;
  background-size: contain;
  height: 35%;
  aspect-ratio: 1;
  pointer-events: none;
  z-index: 1;
}

.polaroid-card>.image {
  background-image: url("https://moderskeppet.se/wp-content/uploads/ms/2007/04/windows-xp-bliss-start-screen-100259803-orig.jpg");
  background-position: center;
  background-size: cover;
  filter: saturate(0);
  transition: filter .5s;
  background-color: white;
  width: 90%;
  height: 80%;
  cursor: pointer;
  border: black solid 1px;
}

.polaroid-card>.image:hover {
  filter: saturate(1);
}

.polaroid-card>.title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20%;
  width: 90%;
  user-select: none;
}

.polaroid-card>.title>p {
  text-align: center;
  margin: 0;
}

 /* Extra small devices (phones, 600px and down) */
 @media only screen and (max-width: 600px) {
  #filter-bar {
    width: 80%;
  }

  .polaroid-card {
    width: 75%;
  }
 }

 /* Small devices (portrait tablets and large phones, 600px and up) */
 @media only screen and (min-width: 600px) {
  #filter-bar {
    width: 65%;
  }

  .polaroid-card {
    width: 95%;
  }
 }
 
 /* Medium devices (landscape tablets, 768px and up) */
 @media only screen and (min-width: 768px) {
  #filter-bar {
    width: 50%;
  }
 }
 
 /* Large devices (laptops/desktops, 992px and up) */
 @media only screen and (min-width: 992px) {
  #filter-bar {
    width: 512px;
  }
 }
 
 /* Extra large devices (large laptops and desktops, 1200px and up) */
 @media only screen and (min-width: 1200px) {
  #filter-bar {
    width: 512px;
  }
 } 
